<template>
  <section id="appointments" class="dark">
    <div class="inner-width">
      <h1 class="section-title">
        Current Appointments
      </h1>
      <p class="error" v-if="error">{{ error }}</p>
      <div class="appointments">
        <div
          class="appointment"
          v-for="(appointment, index) in appointments"
          v-bind:item="appointment"
          v-bind:index="index"
          v-bind:key="appointment._id"
        >
          <i class="icon fas fa-user-tie"></i>
          <table>
            <tr>
              <td class="left">Name:</td>
              <td class="right">{{ appointment.name }}</td>
            </tr>
            <tr>
              <td class="left">Phone:</td>
              <td class="right">{{ appointment.phone }}</td>
            </tr>
            <tr>
              <td class="left">Date:</td>
              <td class="right">{{ appointment.appointmentdate }}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import AppointmentService from "../AppointmentService";

export default {
  name: "Appointment",
  data() {
    return {
      appointments: [],
      error: "",
      name: "",
      phone: "",
      appointmentdate: null,
    };
  },

  async created() {
    try {
      this.appointments = await AppointmentService.getAppointment();
    } catch (err) {
      this.error = err.message;
    }
  },
};
</script>

