<template>
  <section id="contact">
    <div class="inner-width">
      <h1 class="section-title">Appointment Form</h1>
      <form action="#" class="contact-form">
        <input
          class="nameZone"
          type="text"
          id="name"
          v-model="name"
          placeholder="Enter Your Name"
        />
        <input
          class="phoneZone"
          type="text"
          id="phone"
          v-model="phone"
          placeholder="Enter Your Phone Number"
        />
        <input
          class="dateZone"
          type="date"
          id="date"
          v-model="appointmentdate"
          placeholder="Enter Your Appointment Date"
        />
        <input
          type="submit"
          value="Book It!"
          class="btn"
          v-on:click="bookAppointment"
        />
      </form>
    </div>
  </section>
</template>

<script>
import AppointmentService from "../AppointmentService";

export default {
  name: "Appointment",
  data() {
    return {
      appointments: [],
      error: "",
      name: "",
      phone: "",
      appointmentdate: null,
    };
  },

  methods: {
    async bookAppointment() {
      await AppointmentService.insertAppointment(
        this.name,
        this.phone,
        this.appointmentdate
      );
      location.reload();
    },
  },
};
</script>

<style scoped>
section {
  background-color: #f1f1f1;
}

.contact-form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact-form input {
  width: 100%;
  height: 50px;
  margin: 10px 0;
  background-color: #353b48;
  border: none;
  outline: none;
  padding: 20px;
  border-radius: 4px;
  color: #ffffff;
}

.nameZone,
.phoneZone,
.dateZone {
  max-width: calc(50% - 10px);
}

.contact-form .btn {
  width: 180px;
  background-color: transparent;
  color: #48dbfb;
  font-size: 16px;
  border: 2px solid #48dbfb;
  padding: 0;
  margin-left: auto;
  cursor: pointer;
  transition: 0.3s linear;
}

.contact-form .btn:hover {
  background-color: #72a1ac;
  color: #ffffff;
}

.inner-width {
  padding: 0 20px;
}

@media screen and (max-width: 600px) {
  .inner-width {
    padding: 0 20px;
  }

  .nameZone,
  .phoneZone,
  .dateZone {
    max-width: 100%;
  }
}
</style>
