<template>
  <button class="goTop fas fa-arrow-up"></button>
</template>

<script>
import $ from "jquery";

export default {
  name: "GoTopButton",
  mounted() {
    $(document).ready(function() {
      $(window).scroll(function() {
        if (this.scrollY > 20) {
          $(".goTop").fadeIn();
        } else {
          $(".goTop").fadeOut();
        }
      });
      $(".goTop").click(function() {
        scroll(0, 0);
      });
    });
  },
};
</script>

<style scoped>
.goTop {
  position: fixed;
  z-index: 999;
  bottom: 40px;
  right: 40px;
  width: 40px;
  height: 40px;
  background-color: #48dbfb;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  color: #ffffff;
  outline: none;
  display: none;
}
</style>
